import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/add/operator/map';

@Injectable()

export class PostService {
    constructor(private http: Http) {
        console.log('post service initialized');
    }

    getProducts() {
        return this.http.get('https://duka.herokuapp.com/admin/products').map(res => res.json());
    }

    deleteProduct(data) {
        return this.http.post('https://duka.herokuapp.com/admin/removeproduct', data).map(res => res.json());
    }

    addProduct(data) {
        return this.http.post('https://duka.herokuapp.com/admin/addproduct', data).map(res => res.json());
    }

    getPendingDeliveries() {
        return this.http.get('https://duka.herokuapp.com/admin/pending').map(res => res.json());
    }
}


