import { Component, OnInit } from '@angular/core';
import { PostService } from '../services/post.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  //pending deliveried badge
  pending_badge: Number;

  name: String;
  barcode: Number;
  brand: String;
  quantity: Number;
  price: Number;
  category: String;
  desc: String;
  pic_url: String = "./assets/img/icon.png";
  offer: Boolean = false;
  deliverable: Boolean = true;

  products: Products[];
  pending: Products[];

  constructor(public pS: PostService) {

    this.pS.getProducts().subscribe(data => {
      this.products = data.data;
      console.log(data.data[2]);
    });

    this.pS.getPendingDeliveries().subscribe(data => {
      this.pending = data.data;
      this.pending_badge = data.data.length;
    })

  }

  ngOnInit() {
  }

  deleteProduct(barcode) {
    var data = {
      barcode: barcode
    };
    this.pS.deleteProduct(data).subscribe(result => {
      window.location.assign('/');
    });
  }

  submitForm(e) {
    e.preventDefault();

    var data = {
      barcode: this.barcode,
      name: this.name,
      brand: this.brand,
      quantity: this.quantity,
      price: this.price,
      category: this.category,
      desc: this.desc,
      pic_url: this.pic_url,
      offer: this.offer,
      deliverable: this.deliverable
    };

    this.pS.addProduct(data).subscribe(result => {
      console.log(result);
    });

    alert("Product successfully loaded in. Page will now refresh.")

    window.location.assign('/');
  }

}

interface Products {
  name: String;
  quantity: Number;
  brand: String;
  pic_url: String;
  price: Number;
  mdate: Date;
  category: String;
  deliverable: Boolean;
  offer: Boolean;
  barcode: Number;
  desc: String;
  delivered: Boolean;
}



